import React from "react"
import { Link } from "gatsby"

import '../styles/layout.scss'
import '../styles/header.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faInstagram,faTwitter,faFacebookF } from '@fortawesome/free-brands-svg-icons'


const Header = ({ title,props }) => {
  let header
  let nav

  nav = (
    <div id="globalNav" class="header-logo-menu">
      <div id="nav-drawer">
        <input id="nav-input" type="checkbox" class="nav-unshown" />
        <label htmlFor="nav-input" id="nav-open" for="nav-input"><span></span></label>
        <label htmlFor="nav-input" class="nav-unshown" id="nav-close" for="nav-input"></label>
        <div id="nav-content">
        <nav>
          <ul>
            <li><Link to="/">Top</Link></li>
            <li><Link to="/projects/">Project</Link></li>
            <li><Link to="/profile/">Profile</Link></li>
            <li>
              <a href="https://www.instagram.com/otq.studio/"><FontAwesomeIcon icon={faInstagram} aria-label="Instagram Brand Icon" /></a>
              <a href="https://www.twitter.com/otqstudio/"><FontAwesomeIcon icon={faTwitter} aria-label="Twitter Brand Icon" /></a>
              <a href="https://facebook.com/OTQStudio"><FontAwesomeIcon icon={faFacebookF} aria-label="Facebook Brand Icon" /></a></li>
          </ul>
        </nav>
        </div>
      </div>
    </div>
  )

  header = (
    <Link to={`/`}>
      <h1>OTQ Studio</h1>
      <p>Digital Contents Studio</p>
    </Link>
  )

  return (
    <header>
      <div className="flex">
        {header}
        {nav}
      </div>
    </header>
  )
}

export default Header
